
/* THE SECTION OF THE MEDIA QUERY */
@media (max-width:320px){
    /* The section of the body App */
  .App{
    background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
    height:570px;
    width:95%;
    margin:0px auto;
    padding:20px;
  
  }
  
  
  /* The section of the header */
  header{
    background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
    height:90px;
    width:100%;
    border-radius:5px;
    border:1px solid white;
  }
  header h1{
    color:white;
    font-family:monospace;
    text-align:center;
    font-size:25px;
  }
  header p{
    color:white;
    text-align:center;
    font-family:cursive;
    font-size:13px;
  }
  
  
  /* The section of the main */
  main{
    background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
    height: 370px;
    width:100%;
    border-radius:5px;
    margin-top:10px;
    border:1px solid white;
    overflow:auto;
  }
  main::-webkit-scrollbar{
    display:none;
  }
  
  marquee{
    color:orangered;
    font-family:cursive;
    font-weight:normal;
    font-size:12px;
    padding:0;
    text-shadow:1px 1px white;
    text-align:center;
  }
  


  /* The section of the footer */
  footer{
    height:60px;
    width:100%;
    background-image: linear-gradient(to right, #f83600 0%, #f9d423 100%);
    margin-top:10px;
    border-radius:5px;
    border:1px solid white;
  }
  footer input{
    background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
    padding:9px;
    width:60%;
    border:none;
    outline:none;
    margin:10px 10px;
    font-family:cursive;
    border-radius:25px 0 0 0;
    font-size:13px;
    border-top:1px solid yellow;
    border-left:1px solid yellow;
    border-bottom:1px solid yellow;
    color:white;
  }
  footer input::placeholder{
    font-family:cursive;
    font-size:14px;
    color:white;
  }
  
  footer button{
    background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
    padding:9px;
    width:30%;
    border:none;
    outline:none;
    margin-left:-20px;
    font-family:cursive;
    border-radius:0 0 25px 0;
    color:white;
    font-weight:bold;
    font-size:13px;
    border-top:1px solid yellow;
    border-right:1px solid yellow;
    border-bottom:1px solid yellow;
    cursor:pointer;
  }
  footer button:hover{
    background:orange;
  } 
  }



