.list{
    color:white;
    width:350px;
    background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
    height:50px;
    margin:20px auto;
    border-radius:5px;
    border-radius:0 25px;
    cursor:pointer;
}
.list:hover{
    background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
}

/* The section of the list paragraph */
.list p{
    padding:10px;
    color:white;
    font-family:cursive;
}
.list p:hover{
    color:red;
}

/* The section of the media query */
@media screen and (max-width:320px){
    .list{
        color:white;
        width:270px;
        background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
        height:50px;
        margin:20px auto;
        border-radius:5px;
        border-radius:0 25px;
        cursor:pointer;
        outline:none;
    }
    .list:hover{
        background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
    }
    
    /* The section of the list paragraph */
    .list p{
        padding:10px;
        color:white;
        font-family:cursive;
    }
    .list p:hover{
        color:red;
    }
}


